@font-face {
  font-family: 'OpenSans';
  src: url('/src/app/fonts/OpenSans-Regular.woff2') format('woff2');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'OpenSans';
  src: url('/src/app/fonts/OpenSans-SemiBold.woff2') format('woff2');
  font-weight: 600;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: 'OpenSans', sans-serif;
  background-color: #e2e2d3;
}

a {
  text-decoration: none;
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
button,
figure,
input {
  margin: 0;
  padding: 0;
}

.btnReset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
  outline: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}

.listReset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 992px;
  margin: 0 auto;
  padding: 0 15px;
}

.postsTitle {
  margin-bottom: 40px;
  padding-top: 50px;
  font-size: 48px;
  font-weight: 600;
  text-align: center;
  color: #333333;
}

.postsList {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}

.postsItem {
  margin-bottom: 50px;
  padding: 25px;
  position: relative;
  width: 100%;
  border: 1px solid #333333;
  border-radius: 5px;
  background-color: #f9f9c3;
}

.postTitle {
  margin-bottom: 15px;
  font-size: 36px;
  font-weight: 600;
  color: #333333;
}

.postDescription {
  margin-bottom: 25px;
  font-size: 24px;
  font-weight: 400;
  color: #333333;
}

.postViewButton {
  position: absolute;
  right: 15px;
  bottom: 15px;
  padding: 10px;
  font-size: 18px;
  font-weight: 600;
  border: 2px solid #0000ff;
  border-radius: 5px;
  color: #0000ff;
  background-color: #e5fbe3;
  transition: color .3s ease-in-out, background-color .3s ease-in-out;
}

.postViewButton:hover {
  color: #ffffff;
  background-color: #0000ff;
}

.postBackButton {
  position: absolute;
  left: 10%;
  top: 5%;
  padding: 10px;
  font-size: 18px;
  font-weight: 600;
  border: 2px solid #0000ff;
  border-radius: 5px;
  color: #0000ff;
  background-color: #e5fbe3;
  transition: color .3s ease-in-out, background-color .3s ease-in-out;
}

.postBackButton:hover {
  color: #ffffff;
  background-color: #0000ff;
}


